body,
html,
h1,
h2,
h3,
h4 {
  margin: 0;
  font-family: 'Poppins', sans-serif !important;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body {
  overflow-y: auto;
  height: 100%;
}

.makeStyles-paper-11,
.makeStyles-paper-11 h2,
.MuiTypography-body1,
.MuiPaper-root,
.MuiPaper-root h2 {
  color: #2c2560 !important;
}

body {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-position: center center !important;
  background-attachment: fixed !important;
  background-size: cover !important;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Amarante', cursive !important;
}

footer .a:hover {
  fill: '#DA4567';
}

.MuiToolbar-regular {
  color: #e0e3bd;
}

.MuiButton-containedPrimary,
.MuiButton-containedPrimary:hover,
.MuiListItem-button:hover,
.MuiButtonBase-root:hover {
  color: #fff !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.wallet-button {
  color: #2c2560 !important;
  font-family: 'Poppins', sans-serif !important;
  font-size: 1.1rem !important;
  text-align: left !important;
  justify-content: flex-start !important;
  text-transform: none !important;
}
.wallet-button:hover {
  background-color: black !important;
  color: #757ce8 !important;
}
th{
  color: white!important;
  text-align: left!important;
  font-size: 18px!important;
}
td{
  color: white!important;
  text-align: left!important;
}


.depositBtn{
  width: 100%;
  border-radius: 20px!important;
}
.checkedBtn{
  background: rgb(255, 242, 209)!important;
  color: rgb(57, 64, 78)!important;
}
.depositAmountBtn{
  width: 100%;
  border-radius: 20px!important;
  border : 1px solid white!important;
  padding: 0!important;
}
.MuiGrid-spacing-xs-3{
  width : 100%!important;
}
.amountInfo{
  border : 1px solid white;
  border-radius : 20px;
  margin-top : 20px!important;
}
.connectWalletBtn{
  padding: 10px!important;
  margin-top: 20px!important;
  background: linear-gradient(45.18deg, rgb(123, 97, 255) -0.06%, rgba(100, 57, 221, 0.75) 99.95%);
  border-radius : 20px!important;
  width: 100%;
}
.harvestBtn{
  padding: 10px!important;
  margin-top: 20px!important;
  background: rgb(255, 242, 209)!important;
  color : rgb(57, 64, 78)!important;
  border-radius : 20px!important;
  width: 100%;
}

#lock-menu .MuiPaper-root{
  padding-right: 20px;
  padding-left: 20px;
  border-radius: 20px;
  margin-left: -20px;
  margin-top: 15px;
}
#lock-menu p{
  font-size: 18px!important;
}
#lock-menu .MuiPaper-root ul li:hover{
  border-radius: 20px;
  background: rgb(255, 242, 209)!important;
}
#lock-menu .Mui-selected{
  border-radius: 20px;
  background: rgb(255, 242, 209)!important;

}